<template>
  <div class="page-layout videos" id="vApp">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
    </div>
    <div class="row">
      <div class="col-md-12">
        <div id="" v-if="mobileView">
          <div class="text-center text-danger my-2" v-show="isLoading">
            <b-spinner class="align-middle"></b-spinner>
          </div>
          <div class="holder" v-show="!isLoading">
            <div v-if="videoType == 'yt'">
              <div class="notPortraitModa" v-if="videoIsSuccess">
                <div :class="{'bar1':true}"></div>
                <div :class="{'bar':!mobileView}"></div>
                <div :class="{'bar-t':mobileView}"></div>
                <div :class="{'bar-b':mobileView}"></div>
                <div :class="{'bar-b':mobileView, 'bar-dark': pressPlay}"></div>
                <div v-if="pressPlayTransparent" :class="{'bar-b':mobileView, 'bar-transpatent': pressPlayTransparent }"></div>
                <div :class="{'ba r-l':mobileView}"></div>
                <div :class="{'bar-r':mobileView}"></div>
                <div :class="{'bar2':!mobileView}"></div>
                <iframe class="frame youtube-video" id="youtube-video" width="100%" height="500" :src="lessonPath" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
              </div>
            </div>
            <div v-else>
              <div class="notPortraitModa" v-if="videoIsSuccess">
                <h3>
                {{lessonName}}
                </h3>
                <p>{{lessonDescription}}</p>
                <vue-core-video-player v-if="lessonPath" :src="lessonPath"></vue-core-video-player>              
              </div>
            </div>
            <div v-if="!videoIsSuccess">
              <h3>طلبك قيد اﻻنتظار, من فضلك انتظر موافقة الادارة</h3>
              <br>
            </div>
          </div>
          <div v-if="videoType == 'yt'">
            <div v-if="videoIsSuccess && pressPlay" class="mt-4 play-lesson-btn">
              <h3>
                {{lessonName}}
              </h3>
              <p>{{lessonDescription}}</p>
              <b-button @click="playLesson()" variant="light"><i class="fas fa-play pl-2"></i> ابدأ الدرس</b-button>
            </div>
          </div>
        </div>
        <div v-else>
          <h3>مشاهدة الدرس مسموحة على جهازك المحمول فقط !!!</h3>
          <br>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { sendRequest }  from "../utils/RequestHelper.js"
  import $ from "jquery";

  export default {
    name: 'Home',
    data() {
      return {
        videos:[],
        isLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        currentVideo: {},
        lessonPath: null,
        lessonName: null,
        lessonDescription: null,
        mobileView: true,
        isPaused: false,
        videoIsSuccess: false,
        pressPlay: true,
        pressPlayTransparent: false,
        videoType: null
      }
    },
    methods: {
      loadData() {
        this.isLoading = true;
          var successCallback = (response) => {            
            if(response.data.path.includes("https://www.youtube.com/")) {
              this.videoType="yt";
              var url = new URL(response.data.path);
              this.lessonPath = 'https://www.youtube.com/embed/' + url.searchParams.get("v") + '?&autoplay=1&enablejsapi=1&modestbranding=1&version=3&autoplay=0&showinfo=0';
            }
            else if(response.data.path.includes("uploads/")){
              this.videoType="server";
              this.lessonPath = 'http://test.api.mressamsamaka.com/storage/' + response.data.path;
            }
            else{
              this.videoType="server";
              this.lessonPath = 'http://test.api.mressamsamaka.com/test/uploads/' + response.data.path;
            }
            this.lessonName = response.data.name
            this.lessonDescription = response.data.description
            this.isLoading = false;
            this.videoIsSuccess = true;
            if(this.mobileView)
              window.onresize = function() {
                if ((window.outerHeight - window.innerHeight) > 100 ) {
                  var currentInnerHtml;
                  var element = new Image();
                  var elementWithHiddenContent = document.querySelector("#app");
                  var innerHtml = elementWithHiddenContent.innerHTML;
                  element.__defineGetter__("id", function() {
                      currentInnerHtml = ""
                  });
                  this.pressPlay = true;
                  setInterval(function() {
                    if(window.location.href.includes("lesson")){
                      currentInnerHtml = innerHtml;
                      console.clear();
                      window.location.replace("/");
                      elementWithHiddenContent.innerHTML = currentInnerHtml;
                    }
                  }, 2000);
                }
              }
            // }
            // else{
            //   this.isLoading = false;
            //   this.alertType = "danger"
            //   this.alertText = "خطأ - قم بأبلاغ الادارة"
            //   this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            // }
          };
          var errorCallback = (error) => {
            var errorMessage = "حدث خطأ ما ، حاول قريبًا"
            if(error.response.status == 400)
              errorMessage = error.response.data.message;
            this.isLoading = false;
            this.alertType = "danger"
            this.alertText = errorMessage
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('post', '/students/request/media/confirm', true, { media: this.$route.params.lessonID}, successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      playLesson(){
        this.pressPlay = false;
        this.pressPlayTransparent = true;
        $('.youtube-video')[0].contentWindow.postMessage('{"event":"command","func":"' + 'playVideo' + '","args":""}', '*');
        setTimeout(() => {
          this.pressPlayTransparent = false;
        }, 5000);

      }
    },
    created() {
      var userAgent = window.navigator.userAgent,
          platform = window.navigator.platform,
          macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
          windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
          iosPlatforms = ['iPhone', 'iPad', 'iPod'],
          os = null;
      if (macosPlatforms.indexOf(platform) !== -1) {
        os = 'Mac OS';
        this.mobileView = false;
      } else if (iosPlatforms.indexOf(platform) !== -1) {
        os = 'iOS';
        this.mobileView = true;
      } else if (windowsPlatforms.indexOf(platform) !== -1) {
        os = 'Windows';
        this.mobileView = false;
      } else if (/Android/.test(userAgent)) {
        os = 'Android';
        this.mobileView = true;
      } else if (!os && /Linux/.test(platform)) {
        os = 'Linux';
        this.mobileView = false;
      }
      this.isLoading = false;
      $(document).on('keydown', function(event) {
        if (event.keyCode == 9) {   //tab pressed
          event.preventDefault(); // stops its action
        }
      });
      if (this.mobileView) {
        this.loadData();
      }
    }
  }
</script>
<style scoped>
  @import '../assets/css/tableContent.css';
  .holder{
    width: 100%;
    height: 100%;
    position: relative;
}
.palycontrol{
  z-index: 1000;
}
.bar1{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 68px;
  background-color: black;
}
.bar2{    
  position: absolute;
  bottom: 6px;
  right: 0;
  width: 100%;
  height: 50px;
  background-color: black;
}
.frame{
    width: 100%;
    height: 600px;
}

.bar{
  position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.bar-t{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 45%;
}
.bar-b{
    position: absolute;
    bottom: 63px;
    left: 0;
    width: 100%;
    height: 34%;
}
.bar-dark{
  background-color: black;
  bottom: 0px;
  height: 100%;
}
.bar-transpatent{
  background-color: transparent;
  bottom: 0px;
  height: 100%;
}
.bar-l{
    position: absolute;
    left: 0;
    width: 47%;
    height: 90%;
}
.bar-r{
    position: absolute;
    right: 0;
    width: 47%;
    height: 90%;
}
.control-btns{
  display: inline;
}
@media screen and (min-aspect-ratio: 13/9) {
  html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
  .bw-dashboard {
    height: inherit;
  }
}
.disablePlayIcon{
  display: none;
}
.play-lesson-btn{
  color: rgb(173, 173, 173);
  text-align: center;
  bottom: 40%;
  right:10% !important;
  left:10% !important;
  position: absolute;
}

@media screen and (orientation: landscape) {
  .play-lesson-btn{
    right: 30% !important;
    left: 30% !important;
  }
}
</style>