<template>
  <div>
    <b-form @submit.stop.prevent="onSubmit">
      <b-container class="bv-example-row">
        <div class="text-center text-danger my-2" :class="{'alert-div-padding': isLoading}" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>  
        <b-row v-show="!isLoading">
          <b-col lg="12" md="12" class="alert-div">
            <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
              {{alertText}}
            </b-alert>
            <br><br>
          </b-col>
          <br><br>
          <b-col lg="12" md="12">
            <b-form-group id="group-student-email" label="البريد الالكتروني" label-for="student-email">
              <b-form-input
                id="student-email"
                name="student-email"
                v-model="$v.form.email.$model"
                :state="validateState('email')"
                aria-describedby="input-email"
              ></b-form-input>
              <b-form-invalid-feedback id="input-email" v-if='!$v.form.email.required'>
                البريد الالكتروني مطلوب
              </b-form-invalid-feedback>
              <b-form-invalid-feedback id="input-email" v-if='!$v.form.email.email'>
                البريد الالكتروني غير صالح
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <b-form-group id="group-student-password" label="كلمة المرور" label-for="student-password">
              <b-form-input
                id="student-password"
                name="student-password"
                v-model="$v.form.password.$model"
                :state="validateState('password')"
                aria-describedby="input-password"
                type="password"
              ></b-form-input>
              <b-form-invalid-feedback id="input-password" v-if='!$v.form.password.required'>
                كلمة المرور مطلوبه
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <div class="form-footer">
              <b-button class="login-btn" type="submit" variant="primary"> تسجيل الدخول</b-button>
            </div>
            <br>
            <div>
            </div>
          </b-col>
        </b-row>
      </b-container>
    </b-form>
  </div>
</template>

<script>
  import store from "../store/index.js"
  import { sendRequest }  from "../utils/RequestHelper.js"
  import { validationMixin } from "vuelidate";
  import { required, email} from "vuelidate/lib/validators";

  export default {
    mixins: [validationMixin],
    data() {
      return {
        isLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        form: {
          email: null,
          password: null,
        }
      }
    },
    validations: {
      form: {
        email: {
          required,
          email
        },
        password: {
          required,
        },
      }
    },
    methods: {
      getAccountData() {
        var successCallback = (response) => {
          store.dispatch("SET_USERNAME", response.data.first_name + " " + response.data.last_name);
          this.$router.push({
            name: 'home'
          })
          this.isLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "حدث خطأ ما ، حاول قريبًا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          store.commit("SET_TOKEN", null);
        };
        sendRequest('get', '/students/profile', true, null, successCallback, errorCallback);

      },
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      onSubmit() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }
        this.login();
      },
      login() {
        this.isLoading = true;
        var successCallback = (response) => {
          store.commit("SET_TOKEN", response.data.access_token);
          this.getAccountData();
        };
        var errorCallback = (error) => {
          var errorMessage = "البريد الالكتروني او كلمة المرور غير صحيحة"
          if(error.response.data.message){
            errorMessage = error.response.data.message;
          }
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = errorMessage
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/students/login', false, this.form , successCallback, errorCallback);
      },
    },
    created() {
      if(store.getters.authToken) {
        this.$router.push({
          name: 'home'
        })
      }
    }
  }
</script>

<style>
  .login-page{
    width: 100% !important;
  }
  .login-panel{
    width: 35%;
    margin: auto;
  }
  .panel-heading{
    text-align: center;
  }
  .alert-div{
    margin-top: -130px;
  }
  .form-footer{
    padding-top: 10px;
  }
  .alert-div-padding{
    padding: 130px 0px;
  }
  .login-btn, .createAccount-link{
    display: block;
    text-align: center;
  }
</style>