<template>
  <div class="page-layout videos">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
    </div>
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <div class="row asd" v-show="!pageIsLoading">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <div class="tbl-data">عنوان الدرس</div>
                    </th>
                    <th colspan="3">
                      <span class="tbl-data">الوصف</span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="videos.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="videos.length == 0 && !isLoading">
                    <td colspan="7">
                      لا يوجد معلومات
                    </td>
                  </tr>
                  <tr v-for="(item, index) in videos" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <div class="tbl-data">{{item.name}}</div>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.description}}</span>
                    </td>
                    <td width="5%">
                      <div class="form-group row-option">
                        <b-button type="button" size="sm" variant="primary" class="btn btn-primary options-admin-btn request-options-admin-btn" @click="playVideo(item)">مشاهدة</b-button>
                      </div>
                    </td>
                    <td width="5%">
                      <div class="form-group row-option">
                        <b-button type="button" size="sm" variant="outline-primary" class="btn btn-primary options-admin-btn request-options-admin-btn" @click="openSendRequestModal(item)">ارسال طلب</b-button>
                      </div>       
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <b-modal v-model="requestVideoModal" title="طلب مشاهدة درس">
      <p class="my-4">هل تريد مشاهدة هذا الدرس؟</p>
      <template v-slot:modal-footer>
        <b-button size="sm" variant="secondary" @click="requestVideoModal = false">
          الغاء
        </b-button>
        <b-button size="sm" variant="primary" @click="sendVideoRequest()">
          ارسل طلب للادارة
        </b-button>
      </template>
    </b-modal>
  </div>
</template>
<script>
  import { sendRequest }  from "../utils/RequestHelper.js"

  export default {
    name: 'Home',
    data() {
      return {
        videos:[],
        isLoading: true,
        pageIsLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        requestVideoModal: false,
        sendPasswordToShowModal: false,
        currentVideo: {},
      }
    },
    methods: {
      openSendRequestModal(video){
        if(video.id) {
          this.requestVideoModal = true;
          this.currentVideo = video;
        }
      },
      playVideo(video){
        if(video.id) {
          this.$router.push({
            name: 'lesson',
            params: {
              lessonID: video.id
            }
          })
        }
      },
      sendVideoRequest() {
        this.pageIsLoading = true;
        this.requestVideoModal = false;
        if(this.currentVideo.id) {
          this.isLoading = true;
          var successCallback = (response) => {
            this.requestVideoModal = false;
            this.alertType = "success"
            this.alertText = "تم ارسال طلب . انتظر موافقة الادارة"
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.isLoading = false;
            this.pageIsLoading = false;
          };
          var errorCallback = (error) => {
            this.requestVideoModal = false;
            this.isLoading = false;
            this.pageIsLoading = false;
            this.alertType = "danger"
            this.alertText = error.response.data.message
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('post', '/students/request/media', true, { media: this.currentVideo.id }, successCallback, errorCallback);
        }        
      },
      loadData(){
        this.isLoading = true;
        this.pageIsLoading = true;
        var successCallback = (response) => {
          this.videos = response.data;
          this.isLoading = false;
          this.pageIsLoading = false
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. في البريد او كلمه المرور"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/students/medias', true, null , successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      }
    },
    created() {
      this.loadData();
    },
  }
</script>
<style scoped>
  @import '../assets/css/tableContent.css';
</style>