<template>
  <div>
    <b-form @submit.stop.prevent="onSubmit">
      <b-container class="bv-example-row">
        <div class="text-center text-danger my-2" :class="{'alert-div-padding': isLoading}" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>  
        <b-row v-show="!isLoading" v-if="!loginBtnShow">
          <b-col lg="12" md="12" class="alert-div">
            <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
              {{alertText}}
            </b-alert>
            <br><br>
          </b-col>
          <br><br>
          <b-col lg="6" md="12">
            <b-form-group id="group-user-first_name" label="الاسم الاول" label-for="user-first_name">
              <b-form-input
                id="user-first_name"
                name="user-first_name"
                v-model="$v.form.first_name.$model"
                :state="validateState('first_name')"
                aria-describedby="input-first_name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-first_name">
                الاسم الاول مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-user-last_name" label="الاسم الثاني" label-for="user-last_name">
              <b-form-input
                id="user-last_name"
                name="user-last_name"
                v-model="$v.form.last_name.$model"
                :state="validateState('last_name')"
                aria-describedby="input-last_name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-last_name">
                الاسم الثاني مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <b-form-group id="group-user-email" label="البريد الالكتروني" label-for="user-email">
              <b-form-input
                id="user-email"
                name="user-email"
                v-model="$v.form.email.$model"
                :state="validateState('email')"
                aria-describedby="input-email"
              ></b-form-input>
              <b-form-invalid-feedback id="input-email" v-if='!$v.form.email.required'>
                البريد الالكتروني مطلوب
              </b-form-invalid-feedback>
              <b-form-invalid-feedback id="input-email" v-if='!$v.form.email.email'>
                البريد الالكتروني غير صالح
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <b-form-group id="group-user-password" label="كلمة المرور" label-for="user-password">
              <b-form-input
                id="user-password"
                name="user-password"
                v-model="$v.form.password.$model"
                :state="validateState('password')"
                aria-describedby="input-password"
                type="password"
              ></b-form-input>
              <b-form-invalid-feedback id="input-password" v-if='!$v.form.password.required'>
                كلمة المرور مطلوبه
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12">
            <div class="form-footer">
              <b-button class="login-btn" type="submit" variant="primary"> تسجيل </b-button>
            </div>
            <br>
            <div>
              <span class="createAccount-link">لديك حساب؟<b-button variant="link" class="mb-1 mr-0 pr-1" @click="goToLogin('login')" >سجل الدخول لحسابك</b-button></span>
            </div>
          </b-col>
        </b-row>
        <div v-else :class="{'alert-div-padding': loginBtnShow}">
          <b-col lg="12" md="12" class="alert-div">
            <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
              {{alertText}}
            </b-alert>
            <br><br>
          </b-col>
          <br><br>
          <b-button @click="goToLogin('login')" class="login-btn" variant="primary"> سجل الدخول الآن </b-button>
        </div>
      </b-container>
    </b-form>
  </div>
</template>

<script>
  import store from "../store/index.js"
  import { validationMixin } from "vuelidate";
  import { required, email, numeric, sameAs, password} from "vuelidate/lib/validators";
  import { sendRequest }  from "../utils/RequestHelper.js"

  export default {
    mixins: [validationMixin],
    data() {
      return {
        isLoading: false,
        loginBtnShow: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        form: {
          first_name: null,
          last_name: null, 
          email: null,
          password: null,
        }
      }
    },
    validations: {
      form: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        email: {
          required,
          email
        },
        password: {
          required,
        }
      }
    },
    methods: {
      goToLogin() {
        this.$router.push({
          name: 'login'
        })
      },
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      onSubmit() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }
        this.signup();
      },
      signup() {
        this.isLoading = true;
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = "تم انشاء الحساب بنجاح "
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.isLoading = false;
          store.commit("SET_TOKEN", null);
          this.loginBtnShow = true;
        };
        var errorCallback = (error) => {
          var errorMessage = "حدث خطأ ما ، حاول قريبًا"
          if(error.response.data.email == "The email has already been taken.")
            errorMessage = "البريد الالكتروني مستخدم من قبل"
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = errorMessage
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('post', '/students/create', false, this.form , successCallback, errorCallback);
      },
    },
    created() {
      if(store.getters.authToken) {
        this.$router.push({
          name: 'home'
        })
      }
    }
  }
</script>

<style>
  .login-page{
    width: 100% !important;
  }
  .login-panel{
    width: 35%;
    margin: auto;
  }
  .panel-heading{
    text-align: center;
  }
  .alert-div{
    margin-top: -130px;
  }
  .form-footer{
    padding-top: 20px;
  }
  .login-btn{
    display: block;
    text-align: center;
  }
</style>